.flip-card {
    background-color: transparent;
    width: 40%;
    height: 70%;
    /* border: 5px solid #fffbfb; */
    border-radius: 3px;
    perspective: 1000px;
  }
  
  .flip-card-inner {
    position: relative;
    height: 100%;
    width: 100%;
    text-align: center;
    transition: transform 0.4s ease-out;
    transform-style:preserve-3d;
  }

  .flex-ctr-ctr {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%
  }

  .align-ctr {
    justify-content: center;
  }
  
  .flip-card:hover .flip-card-inner {
    transform: rotateY(-180deg) 
  }
  

  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden; /* Safari */
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: rgb(255, 253, 253);
    color: black;
    border-radius: 3px;
    /* overflow: scroll; */
  }
  
  .flip-card-back {
    background-color: rgb(170, 213, 228);
    color: black;
    transform: rotateY(180deg);
    border-radius: 3px;
    /* overflow: scroll; */
  }

  .card-index{
  height: 0;
  text-align: right;
  margin-right: 5px;
  }

  .card-content {
      padding:35px;
  }
  